<template>
  <div>
    <div class="topTitle-wrap">
      <div>
        <span class="tpTitle">资源报告</span>
      </div>
      <div class="" style="margin-right: 20px">
        <ul class="myDatePicker" slot="mySolt">
          <li class="myDatePicker_li">
            <span>日期范围</span>
            <DatePicker @on-change="startTime" :value="dateRange" type="daterange" placeholder=""
                        placement="bottom-end"
                        :clearable=false :editable=false
                        style="width: 200px;"></DatePicker>
          </li>
          <li class="myDatePicker_li">
            <Button class="btnColor" type="primary" icon="archive" @click="confrimDownLoad">下载报告</Button>
          </li>
        </ul>
      </div>
    </div>


    <!-- <top-title title="资源报告">
      <ul class="myDatePicker" slot="mySolt">
        <li class="myDatePicker_li">
          <span>开始时间</span>
          <DatePicker @on-change="startTime" :value="startDay" type="date" placeholder="Select date"
                      style="width: 200px"></DatePicker>
        </li>
        <li class="myDatePicker_li">
          <span>结束时间</span>
          <DatePicker @on-change="endTime" :value="endDay" type="date" placeholder="Select date"
                      style="width: 200px;align-self: center"></DatePicker>
        </li>
        <li  class="myDatePicker_li">
          <Button class="btnColor" icon="archive" type="primary" @click="confrimDownLoad">下载报告</Button>
        </li>

      </ul>
    </top-title>
 -->
    <RadioGroup v-model="selectMode" type="button" size="large" style="margin: 20px" @on-change="changeReport">
      <Radio label="代码位报告"></Radio>
      <Radio label="代码位分组报告"></Radio>
    </RadioGroup>

    <div class="reportContent">
      <div id="main"></div>
      <div class="ract-wrap">
        <div>
          <span class="showRact"></span>
          <span>展现量</span>

        </div>
        <div class="ract-wrap">
          <span class="clickRact"></span>
          <span>点击量</span>

        </div>
      </div>

      <div class="searchReport" v-show="isCode">

        <div style="display: flex;margin: 5px">
          <div style="display: flex;align-items:center;">
            <span style="font-size: 14px;margin-left: 20px;">筛选</span>
          </div>

          <div style="display: flex;align-items:center;">
            <span style="font-size: 14px;margin-left: 20px;vertical-align: middle">代码位分组:</span>
            <Cascader style="display: inline-block;width:200px;" @on-change="changeCodeGroup" :data="codeGroupArr"
                      change-on-select></Cascader>
          </div>

          <div class="l">
            <span style="font-size: 14px;margin-left: 20px ;vertical-align: middle">代码位:</span>

            <Select @on-change="changeCode" v-model="codeModel" style="width:200px" filterable>

              <Option value="-99" label="全部">
              </Option>

              <div>
                <span class="orderId fu">ID号</span>
                <span class="orderName fu">代码位名称</span>
              </div>
              <Option v-for="item in getCodeList" :value="item.fxAdCodeId" :key="item.fxAdCodeId">
                <span class="orderId">{{item.fxAdCodeId}}</span>
                <span class="orderName">{{item.codeName}}</span>
              </Option>
              <Button @click="loadMore" type="text" v-show="isAdd" long>{{textLoadMore}}</Button>
            </Select>


          </div>

        </div>

        <div style="align-self: center;margin-right: 10px">
          <Input v-model="nameLike" placeholder="请输入代码位名称" style="width: 300px"></Input>
          <Button class="btnColor" @click="getFilterData" type="primary" icon="search">搜索</Button>
        </div>


      </div>


      <div class="searchReport" v-show="isCodeGroup">


        <div style="display: flex;margin: 5px">
          <div style="display: flex;align-items:center;">
            <span style="font-size: 14px;margin-left: 20px;vertical-align: middle"> 筛选</span>
          </div>

          <div style="display: flex;align-items:center;">
            <span style="font-size: 14px;margin-left: 20px;vertical-align: middle">代码位分组类型:</span>
            <Select @on-change="changeChanel" v-model="changeModel" style="width:150px">
              <Option value="-99" label="全部">
              </Option>
              <Option value="1" label="站点">
              </Option>
              <Option value="2" label="频道">
              </Option>
            </Select>
          </div>

          <div>
            <span style="font-size: 14px;margin-left: 20px;vertical-align: middle">代码位分组:</span>
            <Cascader style="display: inline-block;width:200px;margin-left: 10px" @on-change="changeCodeGroup2"
                      :data="codeGroupArr" change-on-select></Cascader>
          </div>
        </div>

        <div style="align-self: center;margin-right: 10px">
          <Input v-model="nameLike" placeholder="请输入代码位名称" style="width: 300px"></Input>
          <Button class="btnColor" @click="getFilterData" type="primary" icon="search">搜索</Button>
        </div>


      </div>

      <Table border :columns="reportColumns" :data="adReportList" v-show="isCodeTable"></Table>
      <Table border :columns="codeGroupColumns" :data="adReportList" v-show="isCodeGroupTable"></Table>

      <Page :total="totalCount" :current="pageNo" show-sizer show-total
            @on-change="changePageNo"
            @on-page-size-change="changePageSize"></Page>


    </div>


  </div>


</template>
<style scoped>

  .top-wrap, .DatePicker-wrap {
    display: flex;
    justify-content: space-around;
  }

  .searchReport {
    background: #f7f7f7;
    display: -webkit-flex; /* Safari */
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;

  }

  .ivu-page {
    text-align: right;
    margin-right: 30px;
    padding: 10px 10px;
  }

  .DatePicker-wrap {
    margin-top: 50px;
  }

  .reportContent {
    margin-left: 20px;
    margin-right: 20px;
  }

  .top-wrap li {
    text-align: center;
    /*border: 1px solid #d7f0f2;*/
    padding: 10px;
    color: #c5c5c5;
  }

  #main {
    width: 90%;
    height: 400px;
  }

  .showRact, .clickRact {
    display: inline-block;
    width: 30px;
    height: 15px;
  }

  .showRact {
    background: #044b7c;
  }

  .clickRact {
    background: #ea1107;
  }

  .ract-wrap {
    text-align: center;
    margin-bottom: 10px;
  }

  .ract-wrap div {
    display: inline-block;

  }

  .ract-wrap div span {
    display: inline-block;
    vertical-align: middle;
  }

  .ract-wrap div:nth-child(1) {
    margin-right: 100px;
  }

  .myDatePicker, .myDatePicker_li {
    display: inline-block;
    color: black;
  }

  .myDatePicker {

  }

  .myDatePicker_li {

  }

  .orderName {
    display: inline-block;
    margin-left: 30px;
  }

  .orderId {
    display: inline-block;
    width: 50px;
  }

  .fu {
    color: black;
  }

</style>
<script>
  import topTitle from '@/components/common/topTitle'
  import echarts from 'echarts'

  export default {
    components: {topTitle},
    data() {
      return {
        adClickNum: '',
        adShowNum: '',
        adRate: '',
        startDay: '',
        endDay: '',
        dateRange: [],
        pageSize: 10,
        pageNo: 1,
        codePageSize: 10,
        codePageNo: 1,
        codeTotalPage: 1,
        totalCount: 0,
        dataClickNum: [],
        dataShowNum: [],
        dataAdDateDay: [],
        codeGroupId: '',
        searchName: "",
        codeGroupType: "",
        codeId: '',
        nameLike: '',
        modeType: 1,
        isCode: true,
        isCodeTable: true,
        isCodeGroup: false,
        isCodeGroupTable: false,
        isAdd: true,
        modelGroup: "全部",
        changeModel: "全部",
        textLoadMore: '加载更多',
        selectMode: '代码位报告',
        myChart: '',
        codeGroupArr: [],
        codeModel: '',
        codeArr: [],
        getCodeList: [],
        reportColumns: [
          {
            title: '代码位名称',
            key: 'showName'
          },
          {
            title: '代码位ID',
            key: 'showId'
          },
          {
            title: '展现量',
            key: 'showNum',
            render: (h, params) => {
              return h('span', this.splitNum(params.row.showNum))
            }
          },
          {
            title: '点击量',
            key: 'clickNum',
            render: (h, params) => {
              return h('span', this.splitNum(params.row.clickNum))
            }
          }
          ,
          {
            title: '点击率',
            key: 'rate'
          }
        ],

        codeGroupColumns: [
          {
            title: '代码位分组名称',
            key: 'showName'
          },
          {
            title: '代码位分组类型',
            key: 'showGroupName'
          },
          {
            title: '展现量',
            key: 'showNum',
            render: (h, params) => {
              return h('span', this.splitNum(params.row.showNum))
            }
          },
          {
            title: '点击量',
            key: 'clickNum',
            render: (h, params) => {
              return h('span', this.splitNum(params.row.clickNum))
            }
          }
          ,
          {
            title: '点击率',
            key: 'rate'
          }
        ],
        adReportList: []

      }
    },
    created() {

    },
    mounted() {
      this.startDay = this.getPreDate(-7);//获取一周的时间
      this.endDay = this.getPreDate(-1);
      this.dateRange = [this.startDay, this.endDay];
      this.myChart = echarts.init(document.getElementById('main'));

      this.getCodeListFun();
      this.getCodeGroupStageList();
      this.selectReport();

    },
    methods: {

      initEcharts() {

        var option = {
          title: {
            text: ''
          },

          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }
          },
          color: ["#044b7c", "#ea1107"],


          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: this.dataAdDateDay,
          },
          yAxis: [
            // type: 'value'
            {
              name: '展现量',
              type: 'value'
            },
            {
              name: '点击量',
              type: 'value',

            }
          ],
          minInterval: 1,
          series: [
            {
              name: '展现量',
              data: this.dataShowNum,
              type: 'line',
              areaStyle: {
                color: '#d6eaff',
              },
              lineStyle: {
                color: '#044b7c',
              },

            },
            {
              name: '点击量',
              data: this.dataClickNum,
              yAxisIndex: 1,
              type: 'line',
              areaStyle: {
                color: '#ffd6e1',
              },
              lineStyle: {
                color: '#ea1107',
              },


            }
          ]
        };

        // 使用刚指定的配置项和数据显示图表。
        this.myChart.setOption(option);
      },
      splitNum(num) {
        var reg = /(\d)(?=(?:\d{3})+$)/g;
        var dec = (num + '').split('.');
        if (dec.length == 2) {
          return dec[0].replace(reg, '$1,') + '.' + dec[1];
        } else {
          return dec[0].replace(reg, '$1,');
        }
      },

      getCodeReportList() {
        const url = `/report/getCodeReportList.json?startDay=${this.startDay}&endDay=${this.endDay}&pageSize=${this.pageSize}&pageNo=${this.pageNo}&codeId=${this.codeId}&nameLike=${encodeURIComponent(this.nameLike)}&codeGroupId=${this.codeGroupId}`;
        this.$Http.get(url, {}).then(res => {
          //console.log(res)
          if (res.body.status == 0) {
            this.adReportList = [];
            let chartList = res.body.data.result;
            this.totalCount = res.body.data.totalCount;
            chartList.forEach((item, index) => {
              this.adReportList.push(item);
            })

          }
        });
      },
      getCodeReportChart() {
        const url = `/report/getCodeReportChart.json?startDay=${this.startDay}&endDay=${this.endDay}&codeId=${this.codeId}&nameLike=${encodeURIComponent(this.nameLike)}&codeGroupId=${this.codeGroupId}`;
        this.$Http.get(url, {}).then(res => {
          // console.log(res)
          if (res.body.status == 0) {
            this.dataAdDateDay = [];
            this.dataClickNum = [];
            this.dataShowNum = [];
            let chartList = res.body.data.result;
            let clickNumArr = [];
            let showNumArr = [];
            chartList.forEach((item, index) => {
              clickNumArr.push(item.clickNum);
              showNumArr.push(item.showNum);
              this.dataAdDateDay.push(item.adDateDay);

            })
            this.dataClickNum = clickNumArr;
            this.dataShowNum = showNumArr;
            //this.dataAdDateDay.reverse()
            this.myChart.clear();
            this.initEcharts();
          }
        });
      },
      getCodeGroupReportList() {
        const url = `/report/getCodeGroupReportList.json?startDay=${this.startDay}&endDay=${this.endDay}&pageSize=${this.pageSize}&pageNo=${this.pageNo}&codeGroupType=${this.codeGroupType}&codeGroupId=${this.codeGroupId}&nameLike=${encodeURIComponent(this.nameLike)}`;
        this.$Http.get(url, {}).then(res => {
          //console.log(res)
          if (res.body.status == 0) {
            this.adReportList = [];
            let chartList = res.body.data.result;
            this.totalCount = res.body.data.totalCount;
            chartList.forEach((item, index) => {
              let report = {
                'showNum': item.showNum,
                'clickNum': item.clickNum,
                'rate': item.rate,
                'showName': item.showName,
                'adDateDay': item.adDateDay,
                'showGroupName': item.codeGroupType == 1 ? '站点' : '频道'
              }
              this.adReportList.push(report);
            })

          }
        });
      },
      getCodeGroupReportChart() {
        const url = `/report/getCodeGroupReportChart.json?startDay=${this.startDay}&endDay=${this.endDay}&codeGroupType=${this.codeGroupType}&codeGroupId=${this.codeGroupId}&nameLike=${encodeURIComponent(this.nameLike)}`;
        this.$Http.get(url, {}).then(res => {
          //console.log(res)
          if (res.body.status == 0) {
            this.dataAdDateDay = [];
            this.dataClickNum = [];
            this.dataShowNum = [];
            let chartList = res.body.data.result;
            let clickNumArr = [];
            let showNumArr = [];
            chartList.forEach((item, index) => {
              clickNumArr.push(item.clickNum);
              showNumArr.push(item.showNum);
              this.dataAdDateDay.push(item.adDateDay);

            })
            this.dataClickNum = clickNumArr;
            this.dataShowNum = showNumArr;
            // this.dataAdDateDay.reverse()
            this.myChart.clear();
            this.initEcharts();
          }
        });
      },

      loadMore() {

        if (this.getCodeList.length == 0) {
          return;
        }
        if (this.codePageNo == this.codeTotalPage) {
          this.textLoadMore = "已到最底部"
          return;
        } else {
          this.codePageNo++;
        }
        this.getCodeListFun();
      },

      getPreDate(days) {
        let date = new Date();//获取当前时间
        date.setDate(date.getDate() + days);//设置天数 -1 天
        return date.Format("yyyy-MM-dd");
      },

      selectReport() {
        if (this.startDay == '') {
          this.$Message.error('开始时间不能为空');
          return false;
        }

        if (this.modeType == 1) {
          this.getCodeReportChart();
          this.getCodeReportList();
        } else {
          this.getCodeGroupReportChart();
          this.getCodeGroupReportList();
        }
      },

      selectReportList() {
        if (this.modeType == 1) {
          this.getCodeReportList();
        } else {
          this.getCodeGroupReportList();
        }
      },

      changePageNo(pageNo) {
        // this.pageSize = 10;
        this.pageNo = pageNo;
        this.selectReportList();

      },
      changePageSize(pageSize) {
        this.pageNo = 1;
        this.pageSize = pageSize;
        this.selectReportList();

      },

      getFilterData: function () {

        console.log(this.nameLike);
        this.pageSize = 10;
        this.pageNo = 1;
        //this.selectReportList();
        this.selectReport();

      },


      confrimDownLoad() {

        const token = sessionStorage.getItem('token');

        if (this.modeType == 1) {
          window.location.href = this.ApiUrl + `/report/downloadCodeReport.json?startDay=${this.startDay}&endDay=${this.endDay}&codeId=${this.codeId}&nameLike=${encodeURIComponent(this.nameLike)}&codeGroupId=${this.codeGroupId}&t=${token}`;
        } else {
          window.location.href = this.ApiUrl + `/report/downloadCodeGroupReport.json?startDay=${this.startDay}&endDay=${this.endDay}&codeGroupType=${this.codeGroupType}&codeGroupId=${this.codeGroupId}&nameLike=${encodeURIComponent(this.nameLike)}&t=${token}`;
        }

      },

      getCodeListFun() {
        const url = "/code/getCodeList.json?nameLike=&status&isNative&useStatus=&pageSize=" + this.codePageSize + "&pageNo=" + this.codePageNo;
        this.$Http.get(url, {}).then(res => {
          console.log(res);
          if (res.body.status == 0) {


            this.codeArr = [];

            for (var i = 0; i < res.body.data.result.length; i++) {
              this.getCodeList.push(res.body.data.result[i])
              this.codeArr.push(res.body.data.result[i]);
            }


            this.codeTotalPage = res.body.data.totalPages;
          }

        })
      },

      getCodeGroupStageList() {
        const url = "/common/getCodeGroupStageList.json?";
        this.$Http.get(url, {}).then(res => {
          console.log(res);

          if (res.body.status == 0) {

            let resourceList = res.body.data.resourceList;
            for (let firstData of resourceList) {
              let firstObj = {
                'value': firstData.id,
                'label': firstData.name,
                'children': []
              };
              for (let secondData of firstData.childList) {
                let secondObj = {
                  'value': secondData.id,
                  'label': secondData.name,
                };
                firstObj.children.push(secondObj);
              }
              this.codeGroupArr.push(firstObj);
            }
          }


        })

      },
      getCodeByCodeGroup() {
        const url = `/common/getCodeByCodeGroup.json?codeGroupId=${this.codeGroupId}`;
        this.$Http.get(url, {}).then(res => {
          console.log(res);

          if (res.body.status == 0) {

            let resourceList = res.body.data.resourceList;
            this.getCodeList = [];
            for (let item of resourceList) {
              let codeModel = {
                'fxAdCodeId': item.id,
                'codeName': item.name
              }
              this.getCodeList.push(codeModel)
            }

            this.isAdd = false;

            this.codeTotalPage = res.body.data.totalPages;

          }


        })

      },


      changeCodeGroup(codeGroups) {

        console.log(codeGroups);


        if (codeGroups.length == 2) {
          this.codeGroupId = codeGroups[1];
          this.getCodeByCodeGroup();

        } else if (codeGroups.length == 1) {
          this.codeGroupId = codeGroups[0];
        } else {
          this.codeGroupId = '';
          this.getCodeList = [];
          this.isAdd = true;

          for (var i = 0; i < this.codeArr.length; i++) {
            this.getCodeList.push(this.codeArr[i])
          }
        }
        this.pageNo = 1;
        //this.selectReportList();
        this.selectReport();

      },

      changeCodeGroup2(codeGroups) {
        if (codeGroups.length == 2) {
          this.codeGroupId = codeGroups[1];
        }
        else if (codeGroups.length == 1) {
          this.codeGroupId = codeGroups[0];
        }
        else {
          this.codeGroupId = '';
        }
        this.pageNo = 1;
        // this.selectReportList();
        this.selectReport();

      },
      changeCode(codeId) {

        if (codeId == -99) {
          this.codeId = '';
        } else {
          this.codeId = codeId;
        }

        this.pageNo = 1;
        //this.selectReportList();
        this.selectReport();

      },

      changeChanel(chanel) {

        if (chanel == -99) {
          this.codeGroupType = '';
        } else {
          this.codeGroupType = chanel;
        }
        this.pageNo = 1;
        // this.selectReportList();
        this.selectReport();

      },

      changeReport(data) {
        console.log(data);
        if (data == '代码位报告') {
          this.modeType = 1;
          this.isCode = true;
          this.isCodeTable = true;
          this.isCodeGroup = false;
          this.isCodeGroupTable = false;
        } else {
          this.modeType = 2;
          this.isCode = false;
          this.isCodeTable = false;
          this.isCodeGroup = true;
          this.isCodeGroupTable = true;
        }
        console.log(this.modeType);
        this.nameLike = '';
        this.codeGroupType = '';
        this.codeId = '';
        this.codeGroupId = '';
        this.pageNo = 1;
        this.selectReport();

      },
      startTime(data) {
        if (data.length < 2 || data[0] == '' || data[1] == '') {
          this.$Message.error('请选择日期范围');
          return false;
        }
        this.startDay = data[0];
        this.endDay = data[1];
        this.pageNo = 1;
        this.selectReport();

      }
    }
  }
</script>



